/* eslint-disable no-unused-vars */
import React, { useState } from 'react';
import { connect } from 'react-redux';
import {
  Button,
  CustomInput,
  Input,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from 'reactstrap';
import { NotificationManager } from '../../../../../components/common/react-notifications';
import IntlMessages from '../../../../../helpers/IntlMessages';
import useRequest from '../../../../../hooks/use-request';

const ImageModal = ({ id, modalOpen, toggleModal, accessToken, refresh }) => {
  const [values, setValues] = useState({
    email: id,
    file: { type: 'image/png' },
  });

  const [doRequest, loading] = useRequest({
    url: `/client/add-client-file`,
    method: 'POST',
    headers: {
      Authorization: accessToken,
      'Content-Type': values.file.type,
    },
    data: values.formData,
    nextSuccess: () => {
      NotificationManager.success(
        'Image Added Successfully',
        'Image Operation Success',
        3000,
        null,
        null,
        ''
      );
      toggleModal(false);
      refresh();
    },
    nextError: (error) => {
      NotificationManager.error(
        'error',
        'Image Operation Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  const handleChange = async (event) => {
    const img = event.target.files[0];
    // const imgBuffer = await img.arrayBuffer();
    // console.log(new Blob([imgBuffer], { type: img.type }).stream());

    const formData = new FormData();
    formData.append('image', img, img.name);
    formData.append('email', id);
    formData.append('image_name', img.name);
    setValues({
      ...values,
      image_name: img.name,
      file: img,
      image: {},
      formData,
    });
  };

  const handleSubmit = () => {
    doRequest();
  };

  return (
    <Modal
      isOpen={modalOpen}
      toggle={() => {
        toggleModal(false);
      }}
      size="lg"
      backdrop="static"
    >
      <ModalHeader
        toggle={() => {
          toggleModal(false);
        }}
      >
        <IntlMessages id="pages.add-client-image-modal-title" />
      </ModalHeader>
      <ModalBody>
        <CustomInput
          type="file"
          id="image_name"
          value={values.url}
          onChange={handleChange}
        />
      </ModalBody>
      <ModalFooter>
        <Button
          color="danger"
          outline
          onClick={() => {
            toggleModal(false);
          }}
        >
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button
          id="add-new-user"
          color="primary"
          className={`btn-shadow btn-multiple-state ${
            loading ? 'show-spinner' : ''
          }`}
          onClick={handleSubmit}
        >
          <span className="spinner d-inline-block">
            <span className="bounce1" />
            <span className="bounce2" />
            <span className="bounce3" />
          </span>
          <span className="label">
            <IntlMessages id="pages.submit" />
          </span>
        </Button>
      </ModalFooter>
    </Modal>
  );
};

const mapStateToProps = ({ authUser }) => {
  const { accessToken } = authUser;

  return {
    accessToken,
  };
};

export default connect(mapStateToProps, {})(ImageModal);
