/* eslint-disable camelcase */
import React, { useEffect, useState } from 'react';

import { Row } from 'reactstrap';
import { connect } from 'react-redux';

import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';

import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import {
  getClient,
  getAllService,
  getAllStatus,
  getAllCategory,
} from '../../../../redux/actions';

import ServicesDataTable from './components/services-table';
import DetailsTable from './components/details-table';
import DocumentTable from './components/document-table';
import EditModal from './components/edit-modal';
import AddNewModal from './components/add-new-modal';
import ContextMenuContainer from './components/context-menu-container';

const ClientDetails = ({
  match,
  accessToken,
  currentUser,
  currentClient,
  currentClientServices,
  getClientAction,
  statusFilters,
  serviceFilters,
  categoryFilters,
  getAllServiceAction,
  getAllStatusAction,
  getAllCategoryAction,
  loading,
}) => {
  const { id } = match.params;

  const { data } = currentClientServices;

  const { client_add, client_edit, is_superadmin } = currentUser;

  const [addModalOpen, setAddModalOpen] = useState(false);
  const [editModalOpen, setEditModalOpen] = useState(false);
  const [selectedItem, setSelectedItem] = useState(() => ({
    id: 0,
    date_of_visit: new Date(),
    added_by: '',
    service_name: '',
    category: '',
    subcategory: '',
    status: '',
    remarks: '',
  }));

  useEffect(() => {
    getClientAction(accessToken, id);
    getAllServiceAction(accessToken, {
      ...serviceFilters,
      selectedPageSize: 'max',
    });
    getAllStatusAction(accessToken, {
      ...statusFilters,
      selectedPageSize: 'max',
    });
    getAllCategoryAction(accessToken, {
      ...categoryFilters,
      selectedPageSize: 'max',
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const onContextMenuClick = (e, item) => {
    // log('onContextMenuClick - selected items', selectedItems);
    // log('onContextMenuClick - action : ', item.action);

    switch (item.action) {
      case 'edit':
        setSelectedItem(item.data);
        setEditModalOpen(true);

        break;
      case 'create-new':
        setAddModalOpen(true);
        break;
      default:
        break;
    }
  };

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.client-details" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      <Row>
        <Colxx xxs="12" md="7" className="mb-4">
          <DetailsTable currentClient={currentClient} />
        </Colxx>
        <Colxx xxs="12" md="5" className="mb-4">
          <DocumentTable id={id} showAdd={client_add} showEdit={client_edit} />
        </Colxx>
      </Row>
      <Row>
        <Colxx xxs="12">
          {loading ? (
            <div className="loading" />
          ) : (
            <ServicesDataTable
              id={id}
              items={data.services}
              openNew={() => setAddModalOpen(true)}
              openEdit={() => setEditModalOpen(true)}
              setSelectedItem={setSelectedItem}
              showAdd={client_add}
              showEdit={client_edit}
              isSuperAdmin={is_superadmin}
            />
          )}
          {client_add && (
            <AddNewModal
              id={id}
              modalOpen={addModalOpen}
              toggleModal={setAddModalOpen}
              refresh={() => getClientAction(accessToken, id)}
            />
          )}
          {client_edit && (
            <EditModal
              id={id}
              modalOpen={editModalOpen}
              toggleModal={setEditModalOpen}
              data={selectedItem}
              refresh={() => getClientAction(accessToken, id)}
            />
          )}

          <ContextMenuContainer
            onContextMenuClick={onContextMenuClick}
            showButtons={{ showAdd: client_add, showEdit: client_edit }}
          />
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({
  authUser,
  clientsApp,
  statusApp,
  serviceApp,
  categoryApp,
}) => {
  const { accessToken, currentUser } = authUser;
  const { filters: statusFilters } = statusApp;
  const { filters: serviceFilters } = serviceApp;
  const { filters: categoryFilters } = categoryApp;
  const {
    currentClient,
    currentClientServices,
    loading,
    message,
    error,
  } = clientsApp;
  return {
    accessToken,
    currentUser,
    statusFilters,
    serviceFilters,
    categoryFilters,
    currentClient,
    currentClientServices,
    loading,
    message,
    error,
  };
};

export default connect(mapStateToProps, {
  getClientAction: getClient,
  getAllServiceAction: getAllService,
  getAllStatusAction: getAllStatus,
  getAllCategoryAction: getAllCategory,
})(ClientDetails);
