/* eslint-disable camelcase */
import React from 'react';
import { Card, CardBody, Input, Label, Row } from 'reactstrap';
import { Colxx } from '../../../../../components/common/CustomBootstrap';
import IntlMessages from '../../../../../helpers/IntlMessages';

const getDateWithFormat = (date) => {
  const today = date ? new Date(date) : new Date();
  let dd = today.getDate();
  let mm = today.getMonth() + 1; // January is 0!

  const yyyy = today.getFullYear();
  if (dd < 10) {
    dd = `0${dd}`;
  }
  if (mm < 10) {
    mm = `0${mm}`;
  }
  return `${dd}-${mm}-${yyyy}`;
};

const genderOptions = [
  { value: 'male', label: 'Male' },
  { value: 'female', label: 'Female' },
];

const getGenderLabel = (gender) => {
  if (gender) {
    return genderOptions.find((data) => data.value === gender).label;
  }
  return '';
};

const DetailsTable = ({ currentClient }) => {
  const {
    first_name,
    last_name,
    email,
    phone_number,
    date_of_birth,
    gender,
    address,
    city,
    ethinicity,
    emergency_phone_number,
  } = currentClient;

  return (
    <Card>
      <CardBody>
        <Row>
          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.client-first-name" />
            </Label>
            <Input id="first_name" value={first_name} readOnly />
          </Colxx>
          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.client-last-name" />
            </Label>
            <Input id="last_name" value={last_name} readOnly />
          </Colxx>
        </Row>

        <Row>
          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.client-date-of-birth" />
            </Label>
            <Input
              id="date-of-birth"
              value={getDateWithFormat(date_of_birth)}
              readOnly
            />
          </Colxx>
          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.client-gender" />
            </Label>
            <Input id="gender" value={getGenderLabel(gender)} readOnly />
          </Colxx>
        </Row>

        <Row>
          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.client-ethinicity" />
            </Label>
            <Input id="ethinicity" value={ethinicity} readOnly />
          </Colxx>

          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.user-phone" />
            </Label>
            <Input id="phone_number" value={phone_number} readOnly />
          </Colxx>
        </Row>

        <Row>
          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.user-email" />
            </Label>
            <Input id="email" value={email} readOnly />
          </Colxx>

          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.client-emergency-phone" />
            </Label>
            <Input
              id="emergency_phone_number"
              value={emergency_phone_number}
              placeholder="987XX XX789"
              readOnly
            />
          </Colxx>
        </Row>

        <Label className="mt-4">
          <IntlMessages id="pages.client-address" />
        </Label>
        <Input id="address" type="textarea" rows="6" value={address} readOnly />

        <Row>
          <Colxx className="mt-4 mb-2">
            <Label>
              <IntlMessages id="pages.client-city" />
            </Label>
            <Input id="city" value={city} readOnly />
          </Colxx>
        </Row>
      </CardBody>
    </Card>
  );
};

export default DetailsTable;
