/* eslint-disable no-underscore-dangle */
import React, { useState } from 'react';
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
  Row,
} from 'reactstrap';
import { connect } from 'react-redux';
import Select from 'react-select';

import IntlMessages from '../../../../../helpers/IntlMessages';
import { Colxx } from '../../../../../components/common/CustomBootstrap';

import { log, wait } from '../../../../../helpers/Utils';
import { NotificationManager } from '../../../../../components/common/react-notifications';
import useRequest from '../../../../../hooks/use-request';

const AddNewModal = ({
  id,
  modalOpen,
  toggleModal,
  accessToken,
  currentUser,
  status,
  service,
  category,
  refresh,
}) => {
  const INIT_STATE = () => ({
    email: id,
    added_by: currentUser.id,
    services: '',
    _services: '',
    status: '',
    _status: '',
    category: '',
    _category: '',
    subcategory: [],
    _subcategory: [],
    remarks: '',
  });

  const [values, setValues] = useState(INIT_STATE);

  const handleChange = (event) => {
    setValues({ ...values, [event.target.id]: event.target.value });
  };

  const [doRequest, loading] = useRequest({
    url: '/client/add-client-service',
    method: 'POST',
    headers: {
      Authorization: accessToken,
    },
    data: values,
    nextSuccess: () => {
      NotificationManager.success(
        'Service Added Successfully',
        'Client Operation Success',
        3000,
        null,
        null,
        ''
      );
      refresh();
      toggleModal(false);
      wait(1200, () => {
        setValues(INIT_STATE);
      });
    },
    nextError: (error) => {
      NotificationManager.error(
        error,
        'Client Operation Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  const handleSelectChange = (value, key) => {
    if (!value) return;
    if (key === 'category') {
      log('test', values);
      setValues({ ...values, _subcategory: '', subcategory: '' });
      log('test 2', values);
    }
    log('test 3', values);

    setValues({ ...values, [`_${key}`]: value, [key]: value.id });
  };

  const handleSubmit = () => {
    doRequest();
  };

  return (
    <Modal
      isOpen={modalOpen}
      toggle={() => {
        toggleModal(false);
        setValues(INIT_STATE);
      }}
      size="lg"
      backdrop="static"
    >
      <ModalHeader
        toggle={() => {
          toggleModal(false);
          setValues(INIT_STATE);
        }}
      >
        <IntlMessages id="pages.add-new-client-service-modal-title" />
      </ModalHeader>
      <ModalBody>
        <Row>
          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.app-service-required" />
            </Label>
            <Select
              id="service"
              value={values._services}
              required
              isClearable
              className="react-select mt-2"
              classNamePrefix="react-select"
              getOptionValue={(option) => option.id}
              getOptionLabel={(option) => option.service_name}
              onChange={(option) => handleSelectChange(option, 'services')}
              options={service.data}
              isOptionDisabled={(option) => option.is_enabled === false}
            />
          </Colxx>
          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.app-referred-by" />
            </Label>
            <Input id="refered_by" className="mt-2" onChange={handleChange} />
          </Colxx>
        </Row>

        <Row>
          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.app-category-required" />
            </Label>
            <Select
              id="category"
              value={values._category}
              required
              isClearable
              className="react-select mt-2"
              classNamePrefix="react-select"
              getOptionValue={(option) => option.id}
              getOptionLabel={(option) => option.category_name}
              onChange={(option) => handleSelectChange(option, 'category')}
              options={category.data}
              isOptionDisabled={(option) => option.is_enabled === false}
            />
          </Colxx>
          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.app-sub-category-required" />
            </Label>
            <Select
              id="subcategory"
              className="react-select mt-2"
              classNamePrefix="react-select"
              value={values._subcategory}
              getOptionValue={(option) => option.id}
              getOptionLabel={(option) => option.subcategory_name}
              onChange={(option) => handleSelectChange(option, 'subcategory')}
              options={values._category.sub_category || []}
              isClearable
              required
              isOptionDisabled={(option) => option.is_enabled === false}
            />
          </Colxx>
        </Row>

        <Label className="mt-4">
          <IntlMessages id="pages.app-status-required" />
        </Label>
        <Select
          id="status"
          className="react-select"
          classNamePrefix="react-select"
          value={values._status}
          getOptionValue={(option) => option.id}
          getOptionLabel={(option) => option.status}
          onChange={(option) => handleSelectChange(option, 'status')}
          options={status.data}
          isClearable
          required
          isOptionDisabled={(option) => option.is_enabled === false}
        />

        <Label className="mt-4">
          <IntlMessages id="pages.app-remarks" />
        </Label>
        <Input
          id="remarks"
          type="textarea"
          rows="6"
          value={values.remarks}
          onChange={handleChange}
        />
      </ModalBody>
      <ModalFooter>
        <Button
          color="danger"
          outline
          onClick={() => {
            toggleModal(false);
            setValues(INIT_STATE);
          }}
        >
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button
          id="add-new-user"
          color="primary"
          className={`btn-shadow btn-multiple-state ${
            loading ? 'show-spinner' : ''
          }`}
          onClick={handleSubmit}
        >
          <span className="spinner d-inline-block">
            <span className="bounce1" />
            <span className="bounce2" />
            <span className="bounce3" />
          </span>
          <span className="label">
            <IntlMessages id="pages.submit" />
          </span>
        </Button>
      </ModalFooter>
    </Modal>
  );
};

const mapStateToProps = ({
  authUser,
  clientsApp,
  statusApp,
  serviceApp,
  categoryApp,
}) => {
  const { accessToken, currentUser } = authUser;
  const { currentClient, filters } = clientsApp;
  const { status } = statusApp;
  const { service } = serviceApp;
  const { category } = categoryApp;
  return {
    accessToken,
    currentUser,
    currentClient,
    status,
    service,
    category,
    filters,
  };
};

export default connect(mapStateToProps, {})(AddNewModal);
