import React, { useEffect, useState } from 'react';
import { ContextMenu, ContextMenuTrigger, MenuItem } from 'react-contextmenu';
import { connect } from 'react-redux';

import {
  Card,
  CardBody,
  CardTitle,
  Button,
  Input,
  InputGroup,
  InputGroupAddon,
} from 'reactstrap';
import { NotificationManager } from '../../../../../components/common/react-notifications';
import {
  API_URI,
  defaultWaitTime,
} from '../../../../../constants/defaultValues';
import IntlMessages from '../../../../../helpers/IntlMessages';
import { log, wait } from '../../../../../helpers/Utils';
import useRequest from '../../../../../hooks/use-request';
import FileModal from './file-modal';

function collectData(props) {
  return { data: props.data };
}

const ImageTable = ({ id, accessToken, showAdd, showEdit }) => {
  const [imageModalOpen, setImageModalOpen] = useState(false);
  const [selectedImage, setSelectImage] = useState({ id: -1 });
  const [files, setFiles] = useState([]);

  const [doRequest, loading, error, responseData] = useRequest({
    url: `/client/get-client-file/${id}`,
    headers: {
      Authorization: accessToken,
    },
  });

  const [deleteImage] = useRequest({
    url: `/client/delete-client-file/${selectedImage.id}`,
    method: 'DELETE',
    headers: {
      Authorization: accessToken,
    },
    nextSuccess: () => {
      NotificationManager.success(
        'Image Deleted Successfully',
        'Image Operation Success',
        3000,
        null,
        null,
        ''
      );
      doRequest();
    },
    nextError: (err) => {
      NotificationManager.error(
        err,
        'Image Operation Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  useEffect(() => {
    doRequest();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    setFiles(responseData.files);
  }, [responseData]);

  if (loading) return <div className="loading" />;

  if (error) log(error);

  const onContextMenuClick = (e, item) => {
    switch (item.action) {
      case 'delete':
        setSelectImage(item.data);
        wait(defaultWaitTime, () => {
          deleteImage();
        });
        break;

      default:
        break;
    }
  };

  const onContextMenu = (e, item) => {
    setSelectImage(item.data);

    return true;
  };

  return (
    <Card>
      <CardBody>
        {showAdd && (
          <Button
            className="absolute a top-right-button-container"
            onClick={() => setImageModalOpen(true)}
            color="primary"
          >
            <i className="simple-icon-cloud-upload" />
          </Button>
        )}
        <CardTitle>
          <IntlMessages id="pages.client-documents" />
        </CardTitle>

        <div className="row">
          {files &&
            files.map((item) => {
              return (
                <div className="col-12" key={item.id}>
                  <ContextMenuTrigger
                    id="image_table_id"
                    data={item}
                    collect={collectData}
                  >
                    <InputGroup className="mb-4">
                      <Input value={item.file_name} readOnly />
                      <InputGroupAddon addonType="append">
                        <Button
                          color="primary"
                          href={`${API_URI}/client/download-client-file/${item.id}`}
                        >
                          <i className="simple-icon-cloud-download" />
                        </Button>
                      </InputGroupAddon>
                    </InputGroup>
                  </ContextMenuTrigger>
                </div>
              );
            })}
        </div>

        <ContextMenu
          id="image_table_id"
          style={{ borderRadius: '16px' }}
          onShow={(e) => onContextMenu(e, e.detail.data)}
        >
          {showEdit && (
            <MenuItem
              onClick={onContextMenuClick}
              data={{ action: 'delete' }}
              className="text-primary"
            >
              <i className="simple-icon-trash" /> <span>Delete</span>
            </MenuItem>
          )}
        </ContextMenu>
      </CardBody>
      <FileModal
        id={id}
        modalOpen={imageModalOpen}
        toggleModal={setImageModalOpen}
        refresh={doRequest}
      />
    </Card>
  );
};

const mapStateToProps = ({ authUser }) => {
  const { accessToken } = authUser;
  return { accessToken };
};

export default connect(mapStateToProps, {})(ImageTable);
