import React from 'react';
import { Button, Card, CardBody, CardTitle, Row, NavLink } from 'reactstrap';
import { ContextMenuTrigger } from 'react-contextmenu';
import classnames from 'classnames';

import { Colxx } from '../../../../../components/common/CustomBootstrap';
import Pagination from './pagination';
import IntlMessages from '../../../../../helpers/IntlMessages';
import { API_URI } from '../../../../../constants/defaultValues';

const TableHeading = () => (
  <Card className="d-flex flex-row mb-4">
    <div className="pl-2 d-flex flex-grow-1 min-width-zero">
      <div className="card-body align-self-center d-flex flex-column flex-lg-row justify-content-between min-width-zero align-items-lg-center">
        <p className="mb-0 truncate w-5 w-sm-100">#</p>
        <p className="mb-0 w-12 text-center w-sm-100">Date of visit</p>
        <p className="mb-0 w-12 text-center w-sm-100">Added by</p>
        <p className="mb-0 w-12 text-center w-sm-100">Referral</p>
        <p className="mb-0 w-12 text-center w-sm-100">Referred By</p>
        <p className="mb-0 w-12 text-center w-sm-100">Category</p>
        <p className="mb-0 w-12 text-center w-sm-100">Sub Category</p>
        <p className="mb-0 w-12 text-center w-sm-100">Status</p>
        <p className="mb-0 w-20 text-center w-sm-100">Notes</p>
      </div>
    </div>
  </Card>
);

const TableItem = ({
  item,
  isSelect,
  collect,
  openEdit,
  setSelectedItem,
  showEdit,
}) => {
  const {
    id,
    added_by,
    category,
    date_of_visit,
    remarks,
    service_name,
    status,
    subcategory,
    refered_by,
  } = item;

  return (
    <Colxx xxs="12" className="mb-3">
      <ContextMenuTrigger id="service_table_id" data={item} collect={collect}>
        <Card
          className={classnames('d-flex flex-row', {
            active: isSelect,
          })}
        >
          <div className="pl-2 d-flex flex-grow-1 min-width-zero">
            {/* <pre>{JSON.stringify(item, null, 2)}</pre> */}
            <div className="card-body align-self-center d-flex flex-column flex-lg-row justify-content-between min-width-zero align-items-lg-center">
              <p className="mb-0 truncate w-5 w-sm-100">{id}</p>
              <p className="mb-0 w-12 text-center w-sm-100">{date_of_visit}</p>
              <p className="mb-0 w-12 text-center w-sm-100">{added_by}</p>
              <p className="mb-0 w-12 text-center w-sm-100">{service_name}</p>
              <p className="mb-0 w-12 text-center w-sm-100">{refered_by}</p>
              <p className="mb-0 w-12 text-center w-sm-100">{category}</p>
              <p className="mb-0 w-12 text-center w-sm-100">{subcategory}</p>
              <p className="mb-0 w-12 text-center w-sm-100">{status}</p>
              <p className="mb-0 w-20 text-center w-sm-100">{remarks}</p>
              {showEdit && (
                <p className="mb-0 w-5 w-sm-100 text-center align-itms-end">
                  <i
                    className="m-0 h6 text-primary simple-icon-pencil"
                    onClick={() => {
                      openEdit();
                      setSelectedItem(item);
                    }}
                  />
                </p>
              )}
            </div>
          </div>
        </Card>
      </ContextMenuTrigger>
    </Colxx>
  );
};

function collectData(props) {
  return { data: props.data };
}

const ServicesDataTable = ({
  id,
  items,
  currentPage,
  totalPage,
  onChangePage,
  openNew,
  openEdit,
  setSelectedItem,
  showAdd,
  showEdit,
  isSuperAdmin,
}) => {
  return (
    <Card>
      <CardBody className="my-2">
        {showAdd && (
          <Button
            className="absolute top-right-button-container mx-2"
            onClick={openNew}
            color="primary"
            size="sm"
          >
            <span> Add</span>
          </Button>
        )}
        {isSuperAdmin && (
          <NavLink
            className="absolute top-right-button-container"
            color="secondary"
            size="sm"
            href={`${API_URI}/dashboard/download-client-service-report/${id}`}
            target="_blank"
          >
            <i className="simple-icon-cloud-download h5 text-primary" />
          </NavLink>
        )}
        <CardTitle className="my-3">
          <IntlMessages id="pages.client-service-table" />
        </CardTitle>

        <TableHeading showEdit={showEdit} openEdit={openEdit} />
        <Row>
          {items.map((item) => (
            <TableItem
              key={item.id}
              item={item}
              collect={collectData}
              openEdit={openEdit}
              setSelectedItem={setSelectedItem}
            />
          ))}
          <Pagination
            currentPage={currentPage}
            totalPage={totalPage}
            onChangePage={(i) => onChangePage(i)}
          />
        </Row>
      </CardBody>
    </Card>
  );
};

export default ServicesDataTable;
