/* eslint-disable no-underscore-dangle */
import React, { useEffect, useState } from 'react';
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
  Row,
} from 'reactstrap';
import { connect } from 'react-redux';
import Select from 'react-select';

import IntlMessages from '../../../../../helpers/IntlMessages';
import { Colxx } from '../../../../../components/common/CustomBootstrap';

import { log } from '../../../../../helpers/Utils';
import { NotificationManager } from '../../../../../components/common/react-notifications';
import useRequest from '../../../../../hooks/use-request';

const EditModal = ({
  id,
  data,
  modalOpen,
  toggleModal,
  accessToken,
  currentUser,
  currentClient,
  status,
  service,
  category,
  refresh,
}) => {
  const [values, setValues] = useState({
    ...data,
    email: id,
    added_by: currentUser.id,
    __services: service.data.find(
      (item) => item.service_name === data.service_name
    ),
    __status: '',
    __category: { sub_category: [] },
    __subcategory: [],
  });

  const [formData, setFormData] = useState({});

  useEffect(() => {
    setValues({
      ...data,
      __services: service.data.find(
        (item) => item.service_name === data.service_name
      ),
      __status: status.data.find((item) => item.status === data.status),
      __category: category.data.find(
        (item) => item.category_name === data.category
      ),
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  useEffect(() => {
    setValues({
      ...values,
      __subcategory: values.__category ? values.__category.sub_category : [],
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [values.__category]);

  const handleChange = (event) => {
    log({ [event.target.id]: event.target.value });
    setValues({ ...values, [event.target.id]: event.target.value });
  };

  const [doRequest, loading] = useRequest({
    url: `/client/edit-client-service/${values.id}`,
    method: 'PUT',
    headers: {
      Authorization: accessToken,
    },
    data: formData,
    nextSuccess: () => {
      NotificationManager.success(
        'Service Edited Successfully',
        'Client Operation Success',
        3000,
        null,
        null,
        ''
      );
      refresh();
      toggleModal(false);
    },
    nextError: (error) => {
      NotificationManager.error(
        error,
        'Client Operation Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  const handleSelectChange = (value, key) => {
    if (!value) return;
    log({ [key]: value.id });
    setValues({ ...values, [`__${key}`]: value, [key]: value.id });
  };

  const handleSubmit = () => {
    setFormData({
      email: currentClient.id,
      added_by: currentUser.id,
      remarks: values.remarks,
      services: values.__services.id,
      status: values.__status.id,
      category: values.__category.id,
      subcategory: values.__category.sub_category[0].id,
    });
    doRequest();
  };

  return (
    <Modal
      isOpen={modalOpen}
      toggle={() => {
        toggleModal(false);
        // setValues(INIT_STATE);
      }}
      size="lg"
      backdrop="static"
    >
      <ModalHeader
        toggle={() => {
          toggleModal(false);
        }}
      >
        <IntlMessages id="pages.edit-client-service-modal-title" />
      </ModalHeader>
      <ModalBody>
        <Row>
          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.app-service-required" />
            </Label>
            <Select
              id="service"
              value={values.__services}
              required
              isClearable
              className="react-select mt-2"
              classNamePrefix="react-select"
              getOptionValue={(option) => option.id}
              getOptionLabel={(option) => option.service_name}
              onChange={(option) => handleSelectChange(option, 'services')}
              options={service.data}
            />
          </Colxx>
          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.app-referred-by" />
            </Label>
            <Input id="referred_by" className="mt-2" onChange={handleChange} />
          </Colxx>
        </Row>

        <Row>
          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.app-category-required" />
            </Label>
            <Select
              id="category"
              value={values.__category}
              required
              isClearable
              className="react-select mt-2"
              classNamePrefix="react-select"
              getOptionValue={(option) => option.id}
              getOptionLabel={(option) => option.category_name}
              onChange={(option) => handleSelectChange(option, 'category')}
              options={category.data}
            />
          </Colxx>
          <Colxx>
            <Label className="mt-4">
              <IntlMessages id="pages.app-sub-category-required" />
            </Label>
            <Select
              id="subcategory"
              className="react-select mt-2"
              classNamePrefix="react-select"
              value={values.__subcategory}
              getOptionValue={(option) => option.id}
              getOptionLabel={(option) => option.subcategory_name}
              onChange={(option) => handleSelectChange(option, 'subcategory')}
              options={values.__subcategory || []}
              isClearable
              required
            />
          </Colxx>
        </Row>

        <Label className="mt-4">
          <IntlMessages id="pages.app-status-required" />
        </Label>
        <Select
          id="status"
          className="react-select mt-2"
          classNamePrefix="react-select"
          value={values.__status}
          getOptionValue={(option) => option.id}
          getOptionLabel={(option) => option.status}
          onChange={(option) => handleSelectChange(option, 'status')}
          options={status.data}
          isClearable
          required
        />

        <Label className="mt-4">
          <IntlMessages id="pages.app-remarks" />
        </Label>
        <Input
          id="remarks"
          type="textarea"
          rows="6"
          value={values.remarks}
          onChange={handleChange}
        />
      </ModalBody>
      <ModalFooter>
        <Button
          color="danger"
          outline
          onClick={() => {
            toggleModal(false);
            // setValues(INIT_STATE);
          }}
        >
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button
          id="add-new-user"
          color="primary"
          className={`btn-shadow btn-multiple-state ${
            loading ? 'show-spinner' : ''
          }`}
          onClick={handleSubmit}
        >
          <span className="spinner d-inline-block">
            <span className="bounce1" />
            <span className="bounce2" />
            <span className="bounce3" />
          </span>
          <span className="label">
            <IntlMessages id="pages.submit" />
          </span>
        </Button>
      </ModalFooter>
    </Modal>
  );
};

const mapStateToProps = ({
  authUser,
  clientsApp,
  statusApp,
  serviceApp,
  categoryApp,
}) => {
  const { accessToken, currentUser } = authUser;
  const { currentClient, filters } = clientsApp;
  const { status } = statusApp;
  const { service } = serviceApp;
  const { category } = categoryApp;
  return {
    accessToken,
    currentUser,
    currentClient,
    status,
    service,
    category,
    filters,
  };
};

export default connect(mapStateToProps, {})(EditModal);
